*{
    margin: 0;
    padding: 0;
}
.App{
    display: flex;
    flex-direction: column;
    height:100vh;
}
.app-container{
    flex:1;
    padding: 20px 0px;
}
.hero{
    height: 100%;
    max-height: 100%;
}
.note-editor{
    display: flex;
    flex-direction: column;
    padding: 12.8px;
    border-start-start-radius: 15.6px;
    border-end-start-radius: 15.6px;-webkit-box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
    -moz-box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
    box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
}
.saved-notes{
    border-end-end-radius: 15.6px;
    border-start-end-radius: 15.6px;
    display: flex;
    flex-direction:column;
    gap: 10px;
    flex-wrap: wrap;
    flex-grow: 1;-webkit-box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
    -moz-box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
    box-shadow: 0px 0px 25px 2px rgba(25,118,210,0.26);
    overflow-y: auto!important;
    max-height: 84vh;
    
}.note-card{
    position: relative!important;
    min-height: 90px;
}
.note-btn{
    position: absolute!important;
    right:0!important;
    margin: 0px 5px!important;
    padding: 0!important;
}
.note-title{
    font-size: 24px!important;
    font-weight: bold!important;
}
/* effects */


